import SortingView from '../view/sorting-view.js';//
import CreateFormView from '../view/creation-form-view.js';// 
import WayPointView from '../view/waypoints-view.js';//  
import EventListView from '../view/event-list-view.js';//
import NoPointView from '../view/no-point-view.js';//

import { render, replace } from '../framework/render.js';

export default class BoardPresenter {
  #container = null;
  #pointModel = null;
  #offersModel = null;
  #destinationsModel = null;
  #points = [];

  #eventListComponent = new EventListView();
  #sortComponent = new SortingView();

  constructor({container, pointModel, offersModel, destinationsModel}){
    this.#container = container;
    this.#pointModel = pointModel;
    this.#offersModel = offersModel;
    this.#destinationsModel = destinationsModel;
    this.#points = [...this.#pointModel.get()];
  }

  init() {
    this.#renderBoard();
  }

  #renderBoard(){
    render(this.#sortComponent, this.#container);//
    render(this.#eventListComponent, this.#container);//
    this.#renderPointsList();//
  }

  #renderPointsList() {
    if (this.#points.length) {
      this.#points.forEach((point) => {
        this.#renderPoint(point);
      });
    } else {
      render(new NoPointView(), this.#container);
    }
  }


  #renderPoint(point){// 
    const escKeyDownHandler = (evt)=>{
      if(evt.key === 'Escape'){//   
        evt.preventDefault();//   
        replaceFormToPoint();//    
        document.removeEventListener('keydown',escKeyDownHandler);// 
      }
    };

    const pointComponent = new WayPointView({//  
      point,
      pointDestination: this.#destinationsModel.getById(point.destination),
      pointOffers: this.#offersModel.getByType(point.type),
      onEditClick: ()=>{//   
        replacePointToForm();//   
        document.addEventListener('keydown',escKeyDownHandler);
      }
    });

    const pointEditComponent = new CreateFormView({// 
      point,
      pointDestination: this.#destinationsModel.get(),
      pointOffers: this.#offersModel.get(),
      onFormSubmit:()=>{
        replaceFormToPoint();//    
        document.removeEventListener('keydown',escKeyDownHandler);// 
      },
      onArrowUpClick:()=>{
        replaceFormToPoint();//    
        document.removeEventListener('keydown',escKeyDownHandler);// 
      }
    });
    function replacePointToForm(){
      replace(pointEditComponent,pointComponent);//   
    }
    function replaceFormToPoint(){
      replace(pointComponent,pointEditComponent);//    
    }
    render (pointComponent, this.#container);//   
  }
}

